import React, { Component } from "react";
import { connect } from "react-redux";
import { compose } from "redux";
import { Link } from "react-router-dom";

import notification from "../../../../components/notification";
import { Empty, Tooltip, Input } from "antd";
import Popconfirms from "../../../../components/feedback/popconfirm";
// import Box from '../../components/utility/box';
// import HelperText from "../../../../components/utility/helper-text";
import LayoutWrapper from "../../../../components/utility/layoutWrapper";
import PageHeader from "../../../../components/utility/pageHeader";
import IntlMessages from "../../../../components/utility/intlMessages";
import Scrollbars from "../../../../components/utility/customScrollBar";
import Button from "../../../../components/uielements/button";
import siteActions from "../../../../redux/companyMaster/site/actions";
import TableWrapper from "../../../Tables/antTables/antTable.style";
import CardWrapper, { Box, StatusTag } from "../../../App/app.style";
import getLanguage from "../../../App/MyLanguange";

const Search = Input.Search;

const redirectTo = "/dashboard/setup/menus";

class Sites extends Component {
  state = {
    modal: {
      visible: false,
      id: null
    },
    site: {
      site_code: null,
      site_name: null,
      is_used: null
    },
    search: null
  };

  // Get All data diawal
  componentDidMount() {
    const { getSites } = this.props;
    getSites();
  }

  componentDidUpdate() {
    // Display error notif
    if (this.props.site.error) {
      notification("error", this.props.site.error);
      this.props.clearError();
    }

    // Display success notif
    if (this.props.site.success) {
      notification("success", this.props.site.success);
      this.props.clearSuccess();
    }
  }

  //handle Search Value
  handleSearchValue = e => {
    const value = e.target.value;

    this.setState({ search: value });
  };

  // Handle search
  handleSearch = value => {
    const { getSites } = this.props;
    getSites(1, value);
  };

  // Handle reset search
  handleReset = () => {
    const { getSites } = this.props;
    this.setState({ search: null });
    getSites();
  };

  // Handle action di table
  handleRecord = (action, id) => {
    if (action === "activate") {
      this.props.activateSite(id);
    } else {
      this.props.deactivateSite(id);
    }
  };

  // Handle pagiantion
  handlePageChange = page => {
    const { getSites } = this.props;
    const { search } = this.state;
    getSites(page, search);
  };

  // Handle modal untuk Add / Update
  handleModal = (id_site, site_code, site_name, is_used) => {
    this.setState({
      ...this.state,
      site: {
        ...this.state.site,
        site_code: site_code,
        site_name: site_name,
        is_used: is_used
      },
      modal: {
        ...this.state.modal,
        visible: true,
        id: id_site
      }
    });
  };

  // Handle input change di modal
  handleChange = e => {
    const name = e.target.name;
    const value = e.target.value;

    this.setState({
      site: {
        ...this.state.site,
        [name]: value
      }
    });
  };

  // Handle submit di modal
  handleSubmitModal = action => {
    const site = this.state.site;
    const { updateSite, createSite } = this.props;

    if (action === "update") {
      const id_site = this.state.modal.id;
      updateSite(id_site, site);
    } else {
      createSite(site);
    }

    this.handleClose();
  };

  // Handle button close di modal
  handleClose = () => {
    this.setState({
      modal: {
        ...this.state.modal,
        visible: false
      },
      site: {
        ...this.state.site,
        site_code: null,
        site_name: null,
        is_used: null
      }
    });
  };

  listAction = {
    title: "",
    dataIndex: "id_site",
    rowKey: "id_site",
    width: "10%",
    align: "center",
    render: (text, row) => (
      <div>
        <Tooltip
          placement="topLeft"
          title={<IntlMessages id="tooltip.update" />}
        >
          <Link to={`${this.props.match.path}/update/${text}`}>
            <Button size="small" className="invoiceEdtBtn">
              <i className="ion-android-create" />
            </Button>
          </Link>
        </Tooltip>

        {row.is_active ? (
          <Popconfirms
            title={<IntlMessages id="sites.popconfirm.deactivate" />}
            onConfirm={this.handleRecord.bind(this, "deactivate", text)}
            okText={getLanguage({ id: "yes" })}
            cancelText={getLanguage({ id: "no" })}
          >
            <Tooltip
              placement="topRight"
              title={<IntlMessages id="tooltip.deactivate" />}
            >
              <Button size="small" className="invoiceDltBtn">
                <i className="ion-close-circled" />
              </Button>
            </Tooltip>
          </Popconfirms>
        ) : (
          <Popconfirms
            title={<IntlMessages id="sites.popconfirm.activate" />}
            onConfirm={this.handleRecord.bind(this, "activate", text)}
            okText={getLanguage({ id: "yes" })}
            cancelText={getLanguage({ id: "no" })}
          >
            <Tooltip
              placement="topRight"
              title={<IntlMessages id="tooltip.activate" />}
            >
              <Button size="small" className="invoiceRstBtn">
                <i className="ion-refresh" />
              </Button>
            </Tooltip>
          </Popconfirms>
        )}
      </div>
    )
  };

  render() {
    const columns = [
      {
        title: getLanguage({ id: "code" }),
        dataIndex: "site_code",
        rowKey: "site_code",
        width: "25%",
        render: text => <span>{text}</span>
      },
      {
        title: getLanguage({ id: "name" }),
        dataIndex: "site_name",
        rowKey: "siteName",
        width: "50%",
        render: text => <span>{text}</span>
      },
      {
        title: getLanguage({ id: "status" }),
        dataIndex: "is_active",
        rowKey: "is_active",
        width: "15%",
        align: "center",
        render: text => {
          let className;
          let display;
          if (text === 1) {
            className = "active";
            display = getLanguage({ id: "active" });
          } else {
            className = "nonactive";
            display = getLanguage({ id: "inactive" });
          }
          return <StatusTag className={className}>{display}</StatusTag>;
        }
      }
    ];
    // console.log(this.state.site);

    // Data setup
    const { sites } = this.props.site;
    const dataSource = [];
    const { user } = this.props.auth;
    let { permissions } = user ? user : [];
    permissions = permissions ? permissions : [];

    if (permissions.includes("Update Site") && columns.length === 3) {
      columns.push(this.listAction);
    }

    Object.keys(sites).map((site, index) => {
      return dataSource.push({
        ...sites[site],
        key: site
      });
    });

    // Pagination Setup
    const { current_page, page_size, total, loading } = this.props.site;

    // console.log(this.props);
    return (
      <LayoutWrapper>
        <Box>
          <PageHeader
            title={<IntlMessages id="sites.pageHeader" />}
            marginBottom={true}
          >
            <div>
              {permissions.includes("Create Site") ? (
                <Link to={`${this.props.match.path}/add`}>
                  <Button
                    // loading={loading}
                    // disabled={loading}
                    type="primary"
                    className="mateAddInvoiceBtn"
                  >
                    <IntlMessages id="sites.button.add" />
                  </Button>
                </Link>
              ) : null}
              <Button
                onClick={() => this.props.history.replace(redirectTo)}
                style={{ marginLeft: "5px" }}
              >
                <IntlMessages id="button.back" />
              </Button>
            </div>
          </PageHeader>
          <div className="isoInvoiceTableBtn">
            <Search
              placeholder={
                getLanguage({ id: "code" }) + ", " + getLanguage({ id: "name" })
              }
              onSearch={this.handleSearch}
              style={{ marginRight: "1%" }}
              value={this.state.search}
              onChange={this.handleSearchValue}
            />
            <div
              style={{
                // width: "20%",
                marginTop: "auto",
                marginRight: "1%"
              }}
            >
              <Button
                type="primary"
                onClick={this.handleSearch.bind(this, this.state.search)}
              >
                <IntlMessages id="button.search" />
              </Button>
            </div>

            <div
              style={{
                width: "30%",
                marginTop: "auto",
                marginRight: "27%"
                // marginLeft: "-2%"
              }}
            >
              <Button onClick={this.handleReset}>
                <IntlMessages id="filter.reset" />
              </Button>
            </div>
          </div>

          <CardWrapper>
            {sites.length === 0 && !this.props.site.loading ? (
              <Empty description={<IntlMessages id="sites.table.noData" />} />
            ) : (
              <div className="isoInvoiceTable">
                <Scrollbars style={{ width: "100%" }}>
                  <TableWrapper
                    dataSource={dataSource}
                    columns={columns}
                    loading={this.props.site.loading}
                    className="invoiceListTable"
                    bordered={true}
                    // Set pagination, jika ada page_size mengunakan pagination API
                    // jika tidak ada page_size menggunakan pagination bawaan
                    pagination={
                      page_size
                        ? {
                            current: current_page,
                            pageSize: parseInt(page_size),
                            total: total,
                            onChange: this.handlePageChange.bind(this)
                          }
                        : true
                    }
                  />
                </Scrollbars>
              </div>
            )}
          </CardWrapper>
        </Box>
      </LayoutWrapper>
    );
  }
}

const mapStateToProps = state => {
  return {
    site: state.site,
    auth: state.auth
  };
};

export default compose(connect(mapStateToProps, siteActions))(Sites);
